.logInSecuritySection {
    width: 100%;
    height: auto;
    min-height: 530px;
}

.logInSecurityContainer {
    width: 54%;
    margin-top: 0.5rem;
    height: auto;
    margin-left: auto;
    margin-right: auto;
}

.logInSecurityAccountText {
    color: #555;
    cursor: pointer;
    font-family: "Amazon Ember", Arial, sans-serif;
    font-size: 14px;
    line-height: 20px;
}

.logInSecurityYourAccountText {
    color: #007185;
    cursor: pointer;
    font-family: "Amazon Ember", Arial, sans-serif;
    font-size: 14px;
    line-height: 20px;
    padding-right: 0.5rem;
}

.logInSecurityLogInSecurityText {
    color: #c45500;
    cursor: pointer;
    font-family: "Amazon Ember", Arial, sans-serif;
    font-size: 14px;
    line-height: 20px;
    padding-left: 0.5rem;
}

.logInSecurityYourAccountText:hover {
    color: #c45500;
    text-decoration: underline;
}

.logInSecurityTitleDiv {
    margin-top: 1rem;
}

.logInSecurityTitle {
    font-family: "Amazon Ember", Arial, sans-serif;
    color: #0F1111;
    font-weight: 400;
    font-size: 28px;
    line-height: 36px;
}

.logInSecurityContainerDiv {
    margin-top: 1rem;
    width: 600px;
    height: 306px;
    border-radius: 4px;
    border: 1px #ddd solid;
    background-color: #fff;
    margin-bottom: 2rem;
}

.logInSecurityNameDiv {
    width: 100%;
    height: 102px;
    border-bottom: 1px #ddd solid;
}

.logInSecurityNameTextDiv {
    margin-top: 2rem;
    margin-left: 2rem;
    float: left;
    width: 75%;
}

.logInSecurityNameTitle {
    font-weight: 700;
    color: #111;
    font-size: 13px;
    line-height: 19px;
}

.logInSecurityName {
    margin-top: 0.25rem;
    color: #111;
    font-size: 13px;
    line-height: 19px;
}

.logInSecurityNameBtnDiv {
    float: left;
    margin-top: 2.5rem;
}

.logInSecurityNameBtn {
    width: 70px;
    height: 31px;
    background: #e7e9ec;
    border-radius: 3px;
    border-color: #adb1b8 #a2a6ac #8d9096;
    border-style: solid;
    border-width: 1px;
    cursor: pointer;
    display: inline-block;
    padding: 0;
    text-align: center;
    text-decoration: none;
    vertical-align: middle;
}

