.logInSecurityDoneBtnDiv {
    margin-left: 1rem;
}

.logInSecurityDoneBtn {
    width: 54px;
    height: 31px;
    background: linear-gradient(to bottom, #60bd38, #65d63f);
    border-color: #4da834 #4da834 #4da834;
    color: #111;
    border-radius: 3px;
    border-style: solid;
    border-width: 1px;
    cursor: pointer;
}

.logInSecurityDoneBtn:focus:hover {
    border-style: solid;
    border-width: 1px;
    border-color: #e47911;
}